'use strict';
angular.module('oneImobiliaria', ['ui.router', 'ui.utils.masks']).run([
  '$rootScope', '$state', 'RESOURCES', 'storage', 'UserService', function($rootScope, $state, RESOURCES, storage, UserService) {
    $rootScope.error = false;
    $rootScope.success = false;
    $rootScope.loading = false;
    $rootScope.fileUrl = RESOURCES.API_URL + '/';
    $rootScope.page = '';
    $rootScope.name = '';
    $rootScope.showMenu = false;
    $rootScope.showSubmenu = false;
    $rootScope.appTitle = 'One Consultoria Imobiliária';
    $rootScope.forms = {};
    $rootScope.doLogout = function() {
      return UserService.doLogout();
    };
    $rootScope.toggleMenu = function() {
      return $rootScope.showMenu = !$rootScope.showMenu;
    };
    $rootScope.toggleSubmenu = function() {
      return $rootScope.showSubmenu = !$rootScope.showSubmenu;
    };
    return $rootScope.$on('$stateChangeSuccess', function() {
      var page;
      page = $state.current.name.split('.');
      $rootScope.page = page[1] || '';
      $rootScope.group = storage.getGroup();
      $rootScope.name = storage.getName();
      $rootScope.error = false;
      $rootScope.success = false;
      $rootScope.loading = false;
      $rootScope.showMenu = false;
      $rootScope.showSubmenu = false;
      if ($state.current.requiredLogin && !UserService.isLogged()) {
        UserService.doLogout();
      }
      if (!$state.current.requiredLogin && UserService.isLogged()) {
        $state.go('dashboard.home');
      }
      if ($rootScope.page === 'users' && $rootScope.group !== 'admin') {
        return $state.go('dashboard.home');
      }
    });
  }
]).constant('RESOURCES', {
  'API_URL': 'http://localhost:3000/api'
});

