'use strict';
angular.module('oneImobiliaria').service('$logger', [
  '$rootScope', function($rootScope) {
    return {
      clean: function() {
        $rootScope.success = false;
        $rootScope.error = false;
        return $rootScope.modalError = false;
      },
      error: function(errorString) {
        this.clean();
        return $rootScope.error = errorString;
      },
      modalError: function(errorString) {
        this.clean();
        return $rootScope.modalError = errorString;
      },
      success: function(successString) {
        this.clean();
        return $rootScope.success = successString;
      }
    };
  }
]);

