'use strict';
angular.module('oneImobiliaria').service('sessionInjector', [
  'storage', function(storage) {
    var isUnloggedPage;
    isUnloggedPage = function(config) {
      return config.url.indexOf('auth') > -1 || config.url.indexOf('remember') > -1;
    };
    return {
      request: function(config) {
        if (!isUnloggedPage(config)) {
          config.headers['x-access-token'] = storage.getSessionToken();
        }
        return config;
      }
    };
  }
]);

'use strict';
angular.module('oneImobiliaria').service('storage', function() {
  return {
    isLogged: function() {
      return (localStorage.getItem('x-code-one') !== null) && (localStorage.getItem('code-one') !== null);
    },
    setCodes: function(codes) {
      localStorage.setItem('x-code-one', codes.token);
      localStorage.setItem('code-one', codes.code);
      localStorage.setItem('group-one', codes.type);
      return localStorage.setItem('name-one', codes.name);
    },
    getSessionToken: function() {
      return localStorage.getItem('x-code-one');
    },
    getCode: function() {
      return localStorage.getItem('code-one');
    },
    getName: function() {
      return localStorage.getItem('name-one') || '';
    },
    getGroup: function() {
      return localStorage.getItem('group-one') || '';
    },
    clean: function() {
      localStorage.removeItem('x-code-one');
      localStorage.removeItem('code-one');
      localStorage.removeItem('group-one');
      return localStorage.removeItem('name-one');
    }
  };
});

