'use strict';
angular.module('oneImobiliaria').controller('ClientsCtrl', [
  '$scope', '$rootScope', '$state', '$stateParams', '$filter', '$loading', '$logger', 'storage', 'ClientService', function($scope, $rootScope, $state, $stateParams, $filter, $loading, $logger, storage, ClientService) {
    $scope.client = {};
    $scope.clients = [];
    $scope.edit = true;
    $loading.show();
    if ($stateParams.id) {
      ClientService.get($stateParams.id).then(function(response) {
        $scope.client = response.data;
        $scope.edit = false;
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao buscar clientes. Por favor, atualize a página.');
        return $loading.hide();
      });
    } else {
      ClientService.getAll().then(function(response) {
        $scope.clients = $filter('orderBy')(response.data, '-created');
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao buscar clientes. Por favor, atualize a página.');
        return $loading.hide();
      });
    }
    $scope.canEdit = function() {
      return $scope.edit = true;
    };
    $scope.saveOrUpdate = function() {
      if (!$rootScope.forms.client.$valid) {
        $logger.error('Preencha todos os dados obrigatórios.');
        return;
      }
      $loading.show();
      return ClientService.saveOrUpdate($scope.client).then(function(response) {
        $loading.hide();
        return $state.go('dashboard.clients');
      })["catch"](function(response) {
        $logger.error('Erro ao criar/editar cliente. Por favor, tente novamente.');
        return $loading.hide();
      });
    };
    return $scope.doDelete = function(index) {
      var client;
      client = $scope.clients[index];
      if (client._id === storage.getCode()) {
        return false;
      }
      $loading.show();
      return ClientService["delete"](client._id).then(function(response) {
        $logger.success('Cliente excluído com sucesso!');
        $scope.clients.splice(index, 1);
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao excluir cliente. Por favor, tente novamente.');
        return $loading.hide();
      });
    };
  }
]);

'use strict';
angular.module('oneImobiliaria').controller('LoginCtrl', [
  '$scope', '$rootScope', '$state', '$logger', 'storage', 'UserService', function($scope, $rootScope, $state, $logger, storage, UserService) {
    $scope.user = {};
    return $scope.doLogin = function() {
      if (!$rootScope.forms.login.$valid) {
        $logger.error('Preencha todos os dados obrigatórios.');
        return;
      }
      $scope.user.origin = 'admin';
      return UserService.doLogin($scope.user).then(function(response) {
        storage.setCodes(response.data);
        return $state.go('dashboard.home');
      })["catch"](function(response) {
        return $logger.error('Usuário e/ou senha inválido(s).');
      });
    };
  }
]);

'use strict';
angular.module('oneImobiliaria').controller('PropertiesCtrl', [
  '$scope', '$rootScope', '$state', '$stateParams', '$filter', '$loading', '$logger', 'storage', 'PropertyService', 'LocationService', function($scope, $rootScope, $state, $stateParams, $filter, $loading, $logger, storage, PropertyService, LocationService) {
    var cities;
    $scope.property = {};
    $scope.properties = [];
    $scope.cities = [];
    $scope.states = [];
    cities = [];
    $scope.edit = true;
    $loading.show();
    if ($stateParams.id) {
      PropertyService.get($stateParams.id).then(function(response) {
        $scope.property = response.data;
        $scope.edit = false;
        return LocationService.getAll();
      }).then(function(response) {
        cities = $filter('orderBy')(response.data, 'name');
        return LocationService.getAllStates();
      }).then(function(response) {
        $scope.states = response.data.sort();
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao buscar imóveis. Por favor, atualize a página.');
        return $loading.hide();
      });
    } else {
      PropertyService.getAll().then(function(response) {
        $scope.properties = $filter('orderBy')(response.data, '-created');
        return LocationService.getAll();
      }).then(function(response) {
        cities = $filter('orderBy')(response.data, 'name');
        return LocationService.getAllStates();
      }).then(function(response) {
        $scope.states = response.data.sort();
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao buscar imóveis. Por favor, atualize a página.');
        return $loading.hide();
      });
    }
    $scope.showCities = function(state) {
      return $scope.cities = $filter('orderBy')(cities, {
        state: state
      });
    };
    $scope.canEdit = function() {
      return $scope.edit = true;
    };
    $scope.saveOrUpdate = function() {
      if (!$rootScope.forms.property.$valid) {
        $logger.error('Preencha todos os dados obrigatórios.');
        return;
      }
      $loading.show();
      return PropertyService.saveOrUpdate($scope.property).then(function(response) {
        $loading.hide();
        return $state.go('dashboard.properties');
      })["catch"](function(response) {
        $logger.error('Erro ao criar/editar imóvel. Por favor, tente novamente.');
        return $loading.hide();
      });
    };
    return $scope.doDelete = function(index) {
      var property;
      property = $scope.properties[index];
      if (property._id === storage.getCode()) {
        return false;
      }
      $loading.show();
      return PropertyService["delete"](property._id).then(function(response) {
        $logger.success('Imóvel excluído com sucesso!');
        $scope.properties.splice(index, 1);
        return $loading.hide();
      })["catch"](function(response) {
        $logger.error('Erro ao excluir imóvel. Por favor, tente novamente.');
        return $loading.hide();
      });
    };
  }
]);

